body{
    background: url("./img1.jpg") no-repeat center center fixed;
    background-size: cover;
}
.profile-card{
    width: 400px;
    text-align: center;
    box-shadow: 0 0 10px #00000070;
    position: absolute;
    top: 8%;
    left: 30%;
}
.top-section{
    padding: 20px;
}
img{
    border-radius: 50%;
    width: 150px;
    margin: 0%;
}
.img{
    margin: 10%;
}
.name{
    margin: 10px;
    font-size: 28px;
}
.bottom-section{
    background-color: white;
    padding: 10px;
}
.icon-part{
    border-radius: 50%;
    margin-top:-40px ;
    width: 100%;
}
.about{
    font-size: 20px ;
    margin: 0%;
    padding: 0%;
}
.content{
    padding: 15px 15px;
    margin: 10px;
}
.icon{
   margin-left: 10px;
   margin-right: 10px;
   size: 12px;
}
.icon :hover{
    color: aquamarine;
    background-color: black;
    animation: wave 1.2s infinite;

}
@keyframes wave{
    to{
        transform: scale(1.2);
        opacity: 0.6;
    }
}
.icon span{
    font-size: 30px;
    color: white;
    border-radius: 50%;
    background-color: darkslategrey;
    padding: 15px;
}